.section-third {
    max-width: 100%;
    height: 70rem;
    background: var(--background-color);
    padding: 3rem 20rem;
    max-width: 100%;
  }
  
  .section-third h2 {
    font-size: 5.5rem;
    margin-bottom: 1rem;
    margin-top: 3rem;
    text-align: center;
  }
  
  .section-third p {
    max-width: 40rem;
    height: 9rem;
    margin-bottom: 3rem;
    text-align: center;
    margin: auto;
  }
  
  .section-third .section-third-task {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 4rem;
  }
  
  .section-third .section-third-task .task-1,
  .section-third .section-third-task .task-2,
  .section-third .section-third-task .task-3 {
    background: #fff;
    box-shadow: 0rem 0.4rem 2rem rgba(35, 52, 53, 0.25);
    padding-top: 2rem;
    display: flex;
    text-align: center;
    transition: all 0.3s linear;
    border-radius: 2rem;
    flex-direction: column;
  }
  
  .section-third .section-third-task .task-1 h3,
  .section-third .section-third-task .task-2 h3,
  .section-third .section-third-task .task-3 h3 {
    font-size: 2.5rem;
    text-transform: capitalize;
    margin-top: 2rem;
  }
  
  .section-third .section-third-task .task-1 .img1,
  .section-third .section-third-task .task-2 .img2,
  .section-third .section-third-task .task-3 .imagesss {
    max-width: 15rem;
    height: 15rem;
    transition: all 0.3s;
    margin: auto;
  }
  
  .section-third .section-third-task .task-1 img,
  .section-third .section-third-task .task-2 .img2 {
    height: 16rem;
  }
  
  .section-third .section-third-task .task-3 {
    background: #fff;
    box-shadow: 0rem 0.4rem 2rem rgba(35, 52, 53, 0.25);
  }
  
  .section-third .section-third-task .task-1:hover,
  .section-third .section-third-task .task-2:hover {
    background: #fff;
    box-shadow: 0rem 0.4rem 2rem rgba(35, 52, 53, 0.25);
  }
  
  .section-third p {
    font-size: 1.7rem;
  }
  
  .imagesss {
    float: left;
    width: 250px;
    height: 132px;
    background: transparent url("../../../public/images/rocket.png") center top no-repeat;
    background-size: 150%;
  }
  
  .imagesss:hover {
    background-image: url("../../../public/images/rocket.gif");
  }
  
  .img2 {
    float: left;
    width: 250px;
    height: 132px;
    background: transparent url("../../../public/images/light.png") center top
      no-repeat;
    background-size: 100%;
  }
  
  .img2:hover {
    background-image: url("../../../public/images/light.gif");
  }
  
  .img1 {
    float: left;
    width: 250px;
    height: 132px;
    background: transparent url("../../../public/images/learning.png") center top
      no-repeat;
    background-size: 150%;
  }
  
  .img1:hover {
    background-image: url("../../../public/images/learning.gif");
  }
  
  .img1 .task-3:hover {
    background-image: url("../../../public/images/rocket.gif");
  }
  
  @media screen and (max-width: 1480px) {
    .section-third .section-third-task .task-1 p,
    .section-third .section-third-task .task-2 p,
    .section-third .section-third-task .task-3 p {
      font-size: 1.3rem;
    }
  }
  
  @media screen and (max-width: 1040px) {
    .section-third .section-third-task {
      display: block;
    }
  
    .section-third .section-third-task .task-1 p,
    .section-third .section-third-task .task-2 p,
    .section-third .section-third-task .task-3 p {
      margin: 1.3rem;
    }
  }
  